.container {
  max-width: 700px;
  margin: 0 auto;
  padding: 20px;
}

.container .title{
  margin-bottom: 40px;
}

.navbar{
  padding: 10px 20px;
}

.navbar-nav{
  padding-left: 200px;
}

.navbar-brand{
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 600;
  font-size:20px;
}

.card {
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  border: none;
  margin-bottom: 5px;
  margin-top: 20px;
}

.card-header {
  font-weight: bold;
}

.card-bar {
  display: flex;
  justify-content:end;

}

.card-bar h4{
  font-size: 20px;
  color: gray;
}

.list-group-item{
  cursor: pointer;
}

.result-card {
  text-align: center;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.result-card h4 {
  margin-bottom: 10px;

}

.result-card p {
  margin-bottom: 5px;
}

.restart-button {
  display: block;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.restart-button:hover {
  background-color: #0056b3;
}

.text-success {
  color: #28a745;
}

.text-danger {
  color: #dc3545;
}

.button{
  background-color: green;
}

.progress{
  margin-bottom: 20px;
}

.p-button{
  display: flex;
  justify-content: right;
}

.bg-green{
  background-color: rgb(13, 150, 13);
}
.bg-red{
  background-color: rgb(219, 16, 16);
}
.bg-blue{
  background-color: #77a5d6;
}

.search-btn{
 border-radius: 15px;
 color: #007bff;
 border:1px solid #007bff;
}
.search-btn:hover{
  background-color: #007bff;
  border:1px solid #fff;
 }

 .search-bar{
  border-radius: 15px;
 }

@media screen and(max-width:400px){
  .container .title{
    font-size: 10px;
    background-color: red;
  }
 
}
